@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
.section-brands-list {
	.flex {
		align-items: center;
	}
	.block {
		min-height: 62px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.brand-item {
		display: flex;
		align-items: center;
		width: 100%;
		padding: 10px;
		min-height: 62px;
		svg {
			height: 100%; 
		}
	}
	.section-bottom-button {
		margin-top: 30px;
	}
	@media (min-width: 750px) {
		.block {
			min-height: 110px;
		}
		.brand-item {
			width: auto;
			padding: 0;
			svg {
				height: auto;
			}
		}
		.brand-type-image { 
			width: 100%;
			display: block;
		}
	}
}
.section-brands-list-2 {
	.brand-item {
		position: relative;
		padding: 0;
		padding-bottom: 40px;
		margin-bottom: 20px;
		width: 100%;
	}
	.brand-background {
		display: block;
		width: 100%;
	}
	.brand-item-content {
		@include ab(n,0,0,0);
		background: var(--background-color);
		margin: 0 20px;
		box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
		border-radius: var(--border-radius);
	}
	.brand-logo-container {
		padding: 10px;
		svg {
			display: flex;
		}
	}
	.brand-content-list {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		border-top: 1px solid var(--miscellaneous-color);
		padding: 10px 0;
		li {
			padding: 0 7.5px;
			font-size: 13px;
			line-height: 1.5em;
		}
	}
	@media (min-width: 1000px) {
		.brand-item {
			margin-bottom: 40px;
			img {
				transition: 0.2s all ease-in-out;
				will-change: transform;
			}
			&:hover {
				.brand-background {
					img {
						transform: scale(1.05);
					}
				}
			}
		}

	}
}